<template>
  <div id="app">
    <h1>starpost-ui</h1>
    <h3>Img </h3>
    <sp-img :urlArr="urlArr" @get-urlArr="getUrlArr" @active-index="getActiveIndex"></sp-img>
    <sp-img :urlArr="urlArr1" @get-urlArr="getUrlArr" @active-index="getActiveIndex"></sp-img>
    <sp-dialog :title="title" :visible.sync="visible">
      <sp-img-content :urlArr="spImgArr" :activeIndex="activeIndex" @active-index="getActiveIndex"></sp-img-content>
    </sp-dialog>

    <h3>Menu ()</h3>
    <div class="sp-menu-content">
      <sp-menu-group :menuWidth="['40px', '200px']" :menuStatus.sync="menuStatus">
        <sp-menu :menus="menus" :menuStatus="menuStatus" :select-id="selectId" pagePermissions @select-id="getSelectId" @page-config="getPageConfig" @open="menuOpen" @close="menuClose" />
      </sp-menu-group>
    </div>

    <h3>Hover Button </h3>
    <sp-hover-button></sp-hover-button>

    <h3>Transfer ()</h3>
    <sp-transfer :data="transferData" @clear="clear" :callbakData.sync="callbakData" :title="['1', '2']" />

    <h3>Amap ()</h3>
    <sp-amap :markers="markers" @get-map-form="getMapForm" @get-geocoder="getGeocoder"></sp-amap>


    <h3>CheckBox /</h3>
    <sp-checkbox :checkboxAll="true" :checkedArr="checkArr" v-model.lazy="checkAlled" @change="getCheckArr">&nbsp;&nbsp;</sp-checkbox>
    <div v-for="(item, index) in checkArr" :key="index">
      <sp-checkbox v-model.lazy="item.checked" :checkedArr="checkArr" :checkAlled="checkAlled" @change="getCheckAlled">{{index}}</sp-checkbox>
    </div>

    <h3>Checkbox /</h3>
    <sp-checkbox-button :checkList="checkList" :checkValues="checkValues" @callback="getSaskStatus"></sp-checkbox-button>

    <h3>DataPicker </h3>
    <sp-date-picker :data="datePicker" />

  </div>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      datePicker: [
        {
          data: "2018-01-09",
          money: 28,
          par: 35,
          count: 2200,
          more: true,
          hot: true,
          unit: "kg",
          time: "20-25"
        },
        {
          data: "2018-01-10",
          money: 20,
          par: 22,
          count: 1900,
          more: false,
          hot: false,
          unit: "cbm",
          time: "20-25"
        }
      ],
      //
      transferData: [
        { id: 1, label: "item1" },
        { id: 2, label: "item2" },
        { id: 3, label: "item3" },
        { id: 4, label: "item4" },
        { id: 5, label: "item5" }
      ],
      callbakData: [],
      //
      markers: [
        {
          title: "",
          position: [113.753228, 22.9833],
          address: "()",
          name: "blry",
          phone: "13713262222"
        },
        {
          title: "",
          position: [114.234824, 22.723301],
          address: "",
          name: "blry",
          phone: "13713263333"
        },
        {
          title: "",
          position: [114.07314, 22.62579],
          address: "",
          name: "blry",
          phone: "13713261111"
        }
      ],
      //
      menuStatus: true,
      selectId: "1",
      pageConfig: [],
      menus: [
        {
          label: " 1",
          icon: "icon-wutu",
          url: "javascript:;",
          children: ["aaa", "bbb", "ccc"]
        },
        {
          label: " 2",
          active: false,
          icon: "icon-wutu",
          children: [
            {
              label: " 2-1",
              url: "javascript:;",
              children: ["aaa", "ccc"]
            },
            {
              label: " 2-2",
              active: false,
              children: [
                {
                  label: " 2-2-1",
                  url: "javascript:;"
                },
                {
                  label: " 2-2-2",
                  active: false,
                  children: [
                    {
                      label: "4 2-2-2-1",
                      url: "javascript:;"
                    }
                  ]
                },
                {
                  label: " 2-2-3",
                  active: false,
                  children: [
                    {
                      label: "4 2-2-3-0",
                      url: "javascript:;"
                    }
                  ]
                }
              ]
            }
          ]
        },
        {
          label: " 3",
          active: false,
          icon: "icon-wutu",
          children: [
            {
              label: " 3-1",
              active: false,
              children: [
                {
                  label: " 3-1-1",
                  url: "javascript:;"
                },
                {
                  label: " 3-1-2",
                  url: "javascript:;"
                }
              ]
            }
          ]
        }
      ],
      //
      name: "12",
      visible: false,
      title: "",
      spImgArr: [],
      activeIndex: null, //index
      urlArr: [
        {
          src: "http://pic2.97uimg.com/58pic/19/69/83/38658PICuUm.jpg!w1200",
          title: "1"
        },
        {
          src: "http://pic2.97uimg.com/58pic/21/56/66/15D58PICHdR.jpg!w1200",
          title: "2"
        },
        {
          src: "http://pic2.97uimg.com/58pic/18/23/47/56r58PICHN3.jpg!w1200",
          title: "3"
        }
      ],
      urlArr1: [
        "http://pic2.97uimg.com/58pic/21/56/66/15D58PICHdR.jpg!w1200"
        // 'http://pic2.97uimg.com/58pic/18/23/47/56r58PICHN3.jpg!w1200'
      ],
      imgs2: [],
      checkAlled: false,
      checkArr: [
        { checked: false },
        { checked: false },
        { checked: false },
        { checked: false },
        { checked: false }
      ],
      checkValues: ["01"],
      checkList: [
        {
          id: "01",
          name: ""
        },
        {
          id: "02",
          name: ""
        },
        {
          id: "03",
          name: ""
        }
      ]
    };
  },
  created() {
  },
  watch: {
    callbakData(val) {
      console.log("" + JSON.stringify(val));
    }
  },
  methods: {
    getUrlArr(val) {
      this.visible = true;
      this.spImgArr != val && (this.spImgArr = val);
    },
    //index
    getActiveIndex(index) {
      this.activeIndex = index;
      this.$nextTick(function() {
        this.title =
          this.spImgArr.length > 0 && this.spImgArr[index].title
            ? this.spImgArr[index].title
            : "";
      });
      console.log("index: " + index);
    },
    ///
    getCheckArr(val) {
      this.checkArr = val;
      console.log(":" + JSON.stringify(this.checkArr));
    },
    getCheckAlled(val) {
      this.checkAlled = val;
      console.log(":" + JSON.stringify(this.checkArr));
    },
    ///
    getSaskStatus(val) {
      this.checkValues = val;
      console.log("/ : " + val);
    },
    menuOpen(val) {
      console.log(" ID: " + val);
    },
    menuClose(val) {
      console.log(" ID: " + val);
    },
    //ID
    getSelectId(val) {
      this.selectId = val;
      console.log("ID " + val);
    },
    //
    getPageConfig(val) {
      this.pageConfig = val;
      console.log(" " + JSON.stringify(val));
    },
    //
    getMapForm(val) {
      console.log(" " + JSON.stringify(val));
    },
    //
    getGeocoder(val) {
      console.log(" " + JSON.stringify(val));
    },
    //
    clear() {
      console.log("");
    }
  }
};
</script>

<style lang="scss">
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
#app {
  font-size: 14px;
  width: 60%;
  margin: 40px auto;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
h3 {
  margin-top: 40px;
  margin-bottom: 20px;
}
.sp-select-box {
  margin: 0 auto;
  padding: 20px;
}
input {
  -webkit-appearance: none;
  background-color: #fff;
  background-image: none;
  border-radius: 4px;
  border: 1px solid #dcdfe6;
  box-sizing: border-box;
  color: #606266;
  display: inline-block;
  font-size: inherit;
  height: 30px;
  line-height: 30px;
  outline: none;
  padding: 0 15px;
  transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);
  width: 120px;
  margin-bottom: 10px;
}
</style>



